import React from 'react';
import { Navigate, Outlet } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';

// Danh sch cc vai tr c php truy cp
const ALLOWED_ROLES = ['ad mind'];

function AdminRoute() {
  const { user, isLoadingUsers } = useAuth();

  // Vn hin th loading trong khi ch xc thc
  if (isLoadingUsers) {
    return <div>Verifying access...</div>;
  }

  // Kim tra xem ngi dng  ng nhp V vai tr ca h c trong danh sch c php khng
  if (user && ALLOWED_ROLES.includes(user.phan_loai)) {
    // Nu hp l, cho php render trang con (DashboardPage)
    return <Outlet />;
  }

  // Nu khng hp l (cha ng nhp hoc sai vai tr), iu hng v trang welcome
  // Chng ta c th hin th mt thng bo "Khng c quyn truy cp"  trang welcome sau ny
  return <Navigate to="/welcome" replace />;
}

export default AdminRoute;